import Avatar from "../avatar";
import { mapActions } from "vuex";

export default {
  name: "app-question",
  components: {
    "app-avatar": Avatar,
  },
  props: ["question"],
  data() {
    return {
      sliceEnd: 3,
    };
  },
  computed: {
    slicedAnswers() {
      return (
        this.question &&
        this.question.answers &&
        this.question.answers.length &&
        this.question.answers.slice(0, this.sliceEnd)
      );
    },
  },
  mounted() {},
  methods: {
    ...mapActions(["setSelected"]),
    onQuestion() {
      this.setSelected(this.question);
      this.$router.push([`questions`, `${this.question.id}`]).catch(() => null);
    },
  },
};
